@import '../../../common/variables';
@import '../../../common/mixins';

.clients {

  &__content {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    padding: 64px 34px 61px 36px;

    @include media($mobile_lg) {
      height: 335px;
      justify-content: center;
      padding: 40px 0 40px 0; }

    @include media($mobile_sm) {
      height: 500px; } }

  &__img {

    @include media($mobile_lg) {
      flex-basis: 33%;
      text-align: center; }

    @include media($mobile_sm) {
      flex-basis: 100%; } } }
