@import '../../../common/variables';
@import '../../../common/mixins';

.work {

  & .gellary {
    &__product {
      &::after {
        content: '';
        background-color: rgba(#252525, 0.7); } } }

  &__title {
    font-size: 12px;
    font-weight: $font_bold;
    text-align: center;
    text-transform: uppercase;
    transition: color 0.3s;

    &:hover {
      color: #10c9c3; } }

  &__more {
    padding: 42px 0;
    background-color: #f5f5f5; } }
