@import '../../common/variables';
@import '../../common/mixins';

.footer {
  background-color: #000;
  color: #bcbcbc;
  font-family: $font_nunito;

  @include media($laptop_sm) {
    padding: 0 10px; }

  &__content {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 283px;

    @include media($tablet) {
      flex-direction: column-reverse;
      justify-content: space-around;
      align-items: stretch;
      height: 180px; }

    @include media(565px) {
      height: 290px; } }


  &__inc {
    margin-bottom: 25px;
    color: #f4f4f4;
    font-size: 18px;

    @include media($tablet) {
      margin-bottom: 0;
      font-size: 16px; } }

  &__copyright {
    font-size: 15px;

    @include media($tablet) {
      display: flex; }


    @include media(565px) {
      display: block;
      text-align: center; }

    & p {
      line-height: 20px; } }

  &__email, &__phone {
    display: block;
    font-size: 15px; }

  &__email {
   margin-bottom: 5px; }

  &__left {

    @include media($tablet) {
      display: flex;
      justify-content: space-around; }

    @include media(565px) {
      display: block;
      text-align: center; } }

  &__right {
    display: flex;

    @include media($tablet) {
      display: flex;
      justify-content: space-around;
      align-items: center;
      margin-bottom: 20px; }

    @include media(565px) {
      flex-direction: row-reverse;
      flex-wrap: wrap;
      margin-bottom: 0; } }

  &__contacts {
    @include media(565px) {
      display: flex;
      justify-content: space-between;
      width: 100%;
      margin-bottom: 40px; } }

  &__list {
    padding: 0 135px 0 146px;
    column-count: 2;
    column-gap: 55px;

    @include media($tablet) {
      padding: 0; } }

  &__item {
    font-size: 15px;
    line-height: 23px; }

  &__last-list {
    margin-right: 33px;

    @include media($tablet) {
      margin-right: 0; } }

  & a {
    transition: color 0.3s;
    &:hover,
    &:focus {
      color: #fff; } } }
