@import '../../common/variables';
@import '../../common/mixins';

.form {
  text-align: center;

  @include media($mobile_lg) {
    width: 100%; }

  &__contact {
    display: flex;
    justify-content: space-between;
    @include media($mobile_sm) {
      flex-wrap: wrap; } }

  &__part {
    margin-bottom: 30px; }

  &__part--inline {
    display: inline-block;
    width: 47.762%;

    @include media($mobile_sm) {
      width: 100%; } }

  &__input {
    width: 100%;
    padding: 14px 20px;
    color: #787878;
    font-family: $font_nunito;
    font-size: 15px;

    &::placeholder {
      color: #787878;
      font-family: $font_nunito;
      font-size: 15px; } }

  &__textarea {
    width: 100%;
    min-height: 170px;
    padding: 14px 20px;
    color: #787878;
    font-family: $font_nunito;
    font-size: 15px;
    resize: none; } }
