@import '../../common/variables';
@import '../../common/mixins';

.num {
  background-color: #10c9c3;

  &__content {
    display: flex;
    justify-content: space-between;
    padding: 100px 0 95px 0;
    color: #ffffff;
    font-weight: $font_bold;
    text-transform: uppercase;

    @include media($mobile_lg) {
      align-items: center;
      justify-content: space-around;
      flex-wrap: wrap;
      height: 200px;
      padding: 0; } }

  &__item {
    display: flex;
    justify-content: space-between;

    @include media($mobile_lg) {
      flex-basis: 35%;
      justify-content: left; } }

  &__item-wrapper {
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    p {
      line-height: 18px; } }

  &__img {
    width: 44px;
    height: 44px;
    margin-right: 27px;

    @include media($mobile_lg) {
      height: auto;
      margin-right: 5px;
      text-align: center; }

    img {
      width: 100%;
      @include media($mobile_lg) {
        width: 44px;
        height: 44px; } } }

  &__text {
    font-size: 12px;
    letter-spacing: 1px; }

  &__number {
    font-size: 18px; } }

