@import '../../common/variables';
@import '../../common/mixins';

.about {

  &__header {
    display: flex;
    flex-direction: column;
    justify-content: center;
    position: relative;
    background: #3e3e3e;
    height: 350px;
    text-align: center;

    &::after {
      content: "";
      position: absolute;
      left: 0;
      top: 0;
      z-index: 1;
      width: 100%;
      height: 100%;
      background-image: url("girl.jpg");
      background-repeat: no-repeat;
      background-size: cover;
      background-position: 50% 50%;
      background-attachment: fixed;
      opacity: 0.5; } }

  &__title {
    position: relative;
    z-index: 2;
    margin-bottom: 21px;
    color: #ffffff;
    font-size: 38px;

    @include media($mobile_lg) {
      font-size: 30px; } }

  &__text {
    position: relative;
    z-index: 2;
    color: #ffffff;
    font-family: $font_nunito;
    font-size: 18px;
    line-height: 28px;

    @include media($mobile_lg) {
      font-size: 14px; } }

  &__wrapper {
    position: relative;
    height: 570px;
    display: flex;
    flex-wrap: wrap;

    @include media($mobile_lg) {
      height: 600px; } }

  &__left {
    background: url("copyboock.png");
    background-size: cover;
    background-position: 50% 50%;
    background-repeat: no-repeat;
    width: 50%;

    @include media($mobile_lg) {
      width: 100%;
      height: 50%; } }



  &__right {
    position: relative;
    width: 50%;
    background-color: #f5f5f5;

    @include media($mobile_lg) {
      width: 100%;
      height: 50%; } } }


.aboutme {
  height: 100%;
  width: 100%;

  &__content {
    display: flex;
    align-items: center;
    height: 100%;
    max-width: 453px;
    margin-left: 86px;

    @include media($tablet) {
      margin-left: 20px; }

    @include media($mobile_lg) {
      justify-content: center;
      max-width: 100%;
      margin-left: 0;
      padding: 10px 0; } }

  &__wrapper {

    @include media($mobile_lg) {
      max-width: 453px;
      display: flex;
      flex-direction: column;
      align-items: center; } }

  &__title {
    margin-bottom: 25px;
    font-size: 30px;
    font-weight: $font_bold;

    @include media($mobile_lg) {
      margin-bottom: 10px;
      font-size: 24px; } }

  &__text {
    margin-bottom: 33px;
    color: #787878;
    font-family: $font-nunito;
    font-size: 16px;
    line-height: 24px;

    @include media($mobile_lg) {
      margin-bottom: 10px;
      font-size: 14px;
      text-align: center; } }

  &__paragraph {
    margin-bottom: 27px;

    @include media($mobile_lg) {
      margin-bottom: 10px; } }


  &__signature {
    width: 223px;
    height: 57px;
    margin-bottom: 7px;

    @include media($mobile_lg) {
      margin-bottom: 0;
      text-align: center; }

    img {
      width: 100%; } } }

.question {

  &__content {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 97px 0 100px;

    @include media($mobile_lg) {
      padding: 50px 10px 50px; } } }




