@import '../../common/variables';
@import '../../common/mixins';

.contact {
  background-color: #f5f5f5;

  &__content {
    display: flex;
    flex-direction: column;
    padding: 100px 0 100px;

    @include media($tablet) {
      padding: 100px 10px 100px; }

    @include media($mobile_lg) {
      padding: 50px 5px 50px; } }

  &__wrapper {
    display: flex;
    justify-content: space-between;
    margin-bottom: 100px;

    @include media($mobile_lg) {
      margin-bottom: 50px; }

    @include media($mobile_lg) {
      flex-direction: column;
      align-items: center; } }

  &__info {
    width: 32%;

    @include media($mobile_lg) {
      width: 100%; } }

  &__title {
    margin-bottom: 28px;
    font-size: 30px;

    @include media($mobile_lg) {
      font-size: 24px; } }

  &__text {
    margin-bottom: 27px;
    line-height: 24px; }

  &__text,
  &__data {
    color: #787878;
    font-family: $font_nunito;
    font-size: 16px;

    @include media($mobile_lg) {
      font-size: 14px; } }

  &__data {
    margin-bottom: 15px; }

  &__subtitle {
    color: #000;
    font-weight: $font_bold; }

  &__form {
    max-width: 57.5%;
    flex-grow: 1;
    .form {
      text-align: left;

      @include media($mobile_lg) {
        text-align: center; } }

    @include media($mobile_lg) {
      width: 100%;
      max-width: 100%; } }

  &__map {
    width: 100%;
    height: 470px;
    flex-shrink: 0;
    flex-grow: 1; } }
